/*=======================================
------------Three Dot Menu---------------
========================================*/
    .three-dot-menu-wrapper{
        position: absolute;
        right: 0px;
        top: 0px;
        text-align: center;
        z-index: 9000;
        overflow: visible;
        padding: 25px;
    }

    .three-dot-button{
        width: auto;
        height: auto;
        display: flex;
        flex-direction: row;
        background-color: inherit;
        z-index: 9200;
        position: absolute;
        top: 10px;
        right: 10px;
    }

    .three-dot-menu-wrapper:hover > .dot{
        opacity: .85;
    }

    .dot {
        background-color: #000;
        margin: auto;
        margin-left: 3px;
        margin-right: 3px;
        width: 4px;
        height: 4px;
        border-radius: 50%;
        transition: .25s ease-in-out;
        opacity: .3;
        text-align: center;
        position: relative;
    }

    .three-dot-menu{
        width: 200px;
        height: 50px;
        border-radius: 10px;
        background-color: #f1f1f1;
        position: absolute;
        top:45px;
        right: 15px;
        z-index: 9000;
        display: flex;
        box-shadow: 0px 1px 4px 0px rgba(0,0,0,.35);
        transition: .2s ease-in-out;
    }

    .hidden-three-dot-menu{
        opacity: 0;
        -webkit-transform: translateX(100px) translateY(-50px) scale(0);
                transform: translateX(100px) translateY(-50px) scale(0);
        ;
    }

    .three-dot-menu::before{
        content:'';
        width: 0; 
        height: 0; 
        top:-12.5px;
        right: 15px;
        position: absolute;
        border-left: 15px solid transparent;
        border-right: 15px solid transparent;
        border-bottom: 15px solid #f1f1f1;
    }

    .three-dot-menu h2{
        margin: auto;
        color: #444;
    }

}

@media screen and (min-width:600px){

    .three-dot-menu {
        width: 200px;
